// 测量标准
<template>
  <div class="typeMange">
    <!-- Header -->
    <el-row>
      <el-col :span="24">
        <div class="grid-content bg-purple-dark typeHeader">
          <el-button type="primary" @click="addOne">新增</el-button>
        </div>
      </el-col>
    </el-row>
    <!-- Main -->


    <el-row>
      <el-col :span="24">
        <div class="grid-content bg-purple-dark typeMain">
          <!-- <p style="padding-bottom: 20px">测量标准</p> -->
          <div class="mb" style="padding-bottom: 20px;">
            <el-breadcrumb separator="/">
              <el-breadcrumb-item>测量标准</el-breadcrumb-item>
            </el-breadcrumb>
          </div>
          <el-table :data="tableData" style="width: 100%">
            <!-- <el-table-column label="道岔编号" width="180">
              <template slot-scope="scope">
                <span>{{ scope.row.num }}</span>
              </template>
            </el-table-column> -->
            <el-table-column label="标准名称" width="380">
              <template slot-scope="scope">
                <span >{{ scope.row.turnoutstandard }}</span>
              </template>
            </el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                <el-button size="mini" type="" @click="seeList(scope.$index, scope.row)">查看</el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </el-col>
    </el-row>
    <!-- Alert -->
    <div v-show="editStatus || addStatus" class="typeAlert-bg"></div>
    <div v-show="editStatus || addStatus" class="typeAlert">
      <el-row>
        <el-col>
          <p style="padding-bottom: 20px">修改</p>
        </el-col>
      </el-row>
      <el-row class="edit-item">
        <el-col :span="6">
          <p>质量分析标准：</p>
        </el-col>
        <el-col :span="18">
          <el-dropdown  trigger="click" @command="zlfxbz">
            <span class="el-dropdown-link" style="cursor: pointer;">
              {{save.quality.msg}}<i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item v-for="item in qualitystandard" :key="item.id" :command="item">{{item.msg}}</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </el-col>
      </el-row>

      <el-row class="edit-item">
        <el-col :span="6">
          <p>道岔分类：</p>
        </el-col>
        <el-col :span="18">
          <el-dropdown  trigger="click" @command="dcfl">
            <span class="el-dropdown-link" style="cursor: pointer;">
              {{save.turnout.turnouttypename}}<i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item v-for="item in turnout" :key="item.id" :command="item">{{item.turnouttypename}}</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </el-col>
      </el-row>

      <el-row class="edit-item">
        <el-col :span="6">
          <p>标准名称：</p>
        </el-col>
        <el-col :span="18">
          <el-input v-model="save.turnoutstandard" placeholder="请输入内容"></el-input>
        </el-col>
      </el-row>
      <el-row >
        <el-col  v-show="editStatus" :span="24" class="edit-submit">
          <el-button size="mini" @click="editChange" type="primary">确定修改</el-button>
          <el-button size="mini" @click="editClose">放弃修改</el-button>
        </el-col>
        <el-col  v-show="addStatus" :span="24" class="edit-submit">
          <el-button size="mini" @click="addOk" type="primary">确定</el-button>
          <el-button size="mini" @click="editClose">放弃</el-button>
        </el-col>
      </el-row>
    </div>
  </div>
</template>
<script>
import api from "@/api/index.js";
  export default {
    data() {
      return {
        qualitystandard: [], // 质量分析标准
        turnout: [], // 道岔分类
        save: {
          quality: { // 质量分析标准
            msg: '请选择',
            id: '0'
          },
          turnout: { // 道岔分类
            id: "0",
            isdelete: "",
            reamke: "",
            turnouttypename: "请选择"
          },
          turnoutstandard: '', // 道岔标准名称
          id: "0"
        },
        tableData: [
            // {
            //     num: '0',
            //     qualityid: '质量分析标准id',
            //     turnoutid: '道岔分类id',
            //     turnoutstandard: '道岔标准名称'
            // }
        ],
        editStatus: false,  // 修改弹窗显示
        edit: {             // 修改提交数据
          turnouttypename: '',
          reamke: '',
          id: ''
        },
        addStatus: false,  // 新增弹窗显示
        add: {
          turnouttypename: '',
          reamke: ''
        }

      }
    },
    methods: {
      // 选择质量分析标准
      zlfxbz(choose) {
        console.log(choose)
        this.save.quality = choose
      },
      // 选择道岔分类
      dcfl(choose) {
        console.log(choose)
        this.save.turnout = choose
      },
      // 新增
      addOne() {
        this.addStatus = true;
        this.save = {
          quality: { // 质量分析标准
            msg: '请选择',
            id: '0'
          },
          turnout: { // 道岔分类
            id: "0",
            isdelete: "",
            reamke: "",
            turnouttypename: "请选择"
          },
          turnoutstandard: '', // 道岔标准名称
          id: "0"
        }
      },
      // 确认新增
      addOk() {
        console.log(this.save)
        if(this.save.quality.id == "0") {
          this.$message({
            type: 'error',
            message: '请选择质量分析标准!'
          });
          return;
        }
        if(this.save.turnout.id == "0") {
          this.$message({
            type: 'error',
            message: '请选择质道岔分类!'
          });
          return;
        }
        if(!this.save.turnoutstandard) {
          this.$message({
            type: 'error',
            message: '请输入标准名称!'
          });
          return;
        }
        api.newTurnoutstandard({
          qualityid: this.save.quality.id, 
          turnoutid: this.save.turnout.id,
          turnoutstandard: this.save.turnoutstandard
        }).then(res => {
          console.log(res)
          if(res.data.status == 'ok') {
            this.editStatus = false;
            this.addStatus = false;
            this.$message({
              type: 'success',
              message: '新增成功!'
            });
            this.getLists();
          } else {
            this.$message({
              type: 'error',
              message: '新增失败!'
            });
          }
        })
      },
      // 唤起修改弹窗
      handleEdit(index, row) {
        console.log(index, row);
        this.editStatus = true;
        this.save.id = row.id;
        this.save.turnoutstandard = row.turnoutstandard;
        this.qualitystandard.forEach( item => {
          if(item.id == row.qualityid) {
            this.save.quality = item;
          }
        })
        this.turnout.forEach( item => {
          if(item.id == row.turnoutid) {
            this.save.turnout = item;
          }
        })
      },
      // 唤起删除弹窗
      handleDelete(index, row) {
        console.log(index, row);
        this.$confirm('此操作将永久删除该项, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          api.delTurnoutstandard(row.id).then(res => {
            console.log(res)
            if(res.data.status == 'ok') {
              this.$message({
                type: 'success',
                message: '删除成功!'
              });
              this.getLists();
            } else {
              this.$message({
                type: 'error',
                message: '删除失败!'
              });
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      },
      // 进入下一级列表
      seeList(index, row) {
        console.log(index, row);
        this.$router.push({ 
          name: 'measureStandardTemplate', 
          params: { turnoutstandardid:  row.id}
        })
      },
      // 确认修改
      editChange() {
        this.editStatus = false;
        this.addStatus = false;
        console.log(this.save)
        api.editTurnoutstandard(this.save.id, {
          qualityid: this.save.quality.id, 
          turnoutid: this.save.turnout.id,
          turnoutstandard: this.save.turnoutstandard
        }).then(res => {
          console.log(res)
          if(res.data.status == 'ok') {
            this.$message({
              type: 'success',
              message: '修改成功!'
            });
            this.getLists();
          } else {
            this.$message({
              type: 'error',
              message: '修改失败!'
            });
          }
        })
      },
      // 取消修改
      editClose() {
        this.editStatus = false;
        this.addStatus = false;
        this.$message({
          type: 'info',
          message: '已放弃操作'
        });  
      },
      // 获取列表
      getLists() {
        api.getTesStandard().then(res => {
            console.log(res.data.result)
            this.tableData = res.data.result
            // res.data.result.forEach((item, index) => {
            //     this.tableData.push({
            //         num: index + 1,
            //         qualityid: item.qualityid,
            //         turnoutid: item.turnoutid,
            //         turnoutstandard: item.turnoutstandard,
            //         id: item.id
            //     })
            // })
        })
      },
      // 获取质量标准分类
      getQualitystandard() {
        api.getQualitystandard().then(res => {
          console.log(res.data.result);
          this.qualitystandard = res.data.result;
        })
      },
      // 获取道岔列表
      getTurnouttype() {
        api.getTurnouttype().then(res => {
          console.log(res.data.result)
          this.turnout = res.data.result;
        })
      },
      // 创建道岔标准
      newTurnoutstandard() {
        api.newTurnoutstandard({
          qualityid: '',
          turnoutid: '',
          turnoutstandard: ''
        }).then(res => {
          console.log(res);
        })
      }
    },
    created() {
      // 获取列表
      this.getLists();
      this.getQualitystandard();
      this.getTurnouttype();
    },
  }
</script>

<style lang="stylus" scoped>
  .typeHeader
      display flex
      justify-content flex-end
      padding-bottom 20px
  .typeMain
      background #fff
      border-radius 12px
      overflow hidden
      padding 20px
      .el-table
        border 1px solid rgba(0,0,0,.1)
  .typeAlert-bg
      width 100%
      height 100vh
      position fixed
      left 0
      top 0
      z-index 1
      background rgba(0,0,0,.5)
  .typeAlert
    width 470px
    height 200px
    background #fff
    position fixed
    left 50% 
    top 50% 
    transform translate(-50%, -50%)
    z-index 9
    padding 20px
    border-radius 4px
    .edit-item
      display flex
      align-items center
      padding 6px 0
    .edit-submit
      display flex
      justify-content flex-end
      padding-top 20px
  .mb
    padding 10px 0

</style>


